.tooltiptext[data-v-1a718efe] {
    visibility: hidden;   
    font-size: 20px; 
    background-color: #484545;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 3px;
    top: -15px;    
    

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
}
.tooltip:hover .tooltiptext[data-v-1a718efe] {
    visibility: visible;
}
.tooltiptext[data-v-1a718efe]::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 10%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #484545 transparent transparent transparent;
}

